.filter-container {
  width: 70%;
  height: fit-content;
  margin: auto;
  border-radius: 10px;
  box-shadow: 1px 1px 7px lightgray;

  .kategori {
    text-align: start;
    margin: 0;
    padding: 10px 0 0 20px;
  }

  .filter-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .l-side-f {
      display: flex;
      margin: auto 10px;

      .menu {
        width: 50px;
        height: 50px;
        margin: 20px 10px 0 10px;
        border-radius: 7px;
        border: 1px solid rgb(223, 223, 223);
      }
    }

    .r-side-f {
      display: flex;
      align-items: center;
      justify-content: center;
      column-gap: 20px;
      width: 50%;
      height: 90px;
      margin: 5px 15px;

      div {
        margin: 15px 0;
      }

      #filter {
        height: 30px;
        width: 100px;
      }
    }
  }
}

@media (min-width: 768px) {
  .filter-container {
    width: 70%;
    height: fit-content;
    margin: auto;
    border-radius: 10px;
    box-shadow: 1px 1px 7px lightgray;

    .kategori {
      text-align: start;
      margin: 0;
      padding: 10px 0 0 20px;
    }

    .filter-wrapper {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;

      .l-side-f {
        display: flex;
        margin: auto 10px;

        .menu {
          width: 100px;
          height: 100px;
          margin: 10px;
          border-radius: 7px;
          border: 1px solid rgb(223, 223, 223);
        }
      }

      .r-side-f {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 20px;
        width: 50%;
        margin: 20px 10px;

        div {
          margin: 15px 0;
        }

        #filter {
          height: 40px;
          width: 200px;
        }
      }
    }
  }
}
